<template>
  <div class="home">
    <!-- 在public/index.html 中设置user-scalable=no 
    <meta name="viewport" content="width=device-width,initial-scale=1.0,user-scalable=no"> 
    不然用户双击可以缩放
    之前遇到的flex宽度留白问题就是因为没注意这个,事实上是自己双击缩放了
    -->
    <div class="input">
      <input type="text" class="text" v-model="value" placeholder="请输入快递单号" @focus="isShowHistory=true" @blur="cancelFocus" value="">
      <div class="close-icon" @click="value=''"></div>
      <div class="search-icon" @click="search"></div>
      <div class="history" v-if="isShowHistory&&expressCode.length>=0">
        <div class="historyItem" v-for="code,i in expressCode" :key="i" @click="chooseExpressCode(code)">{{code}}</div>
      </div>
    </div>
    
    <div class="searchResult">
      <div class="content" v-if="data.length>0">
        <div class="header">
          <div class="time">
            <div class="text">时间</div>
            <div class="icon" @click="sort=!sort">
              <span class="topT" :class="{active:sort}"></span>
              <span class="botT" :class="{active:!sort}"></span>
            </div>
          </div>
          <div class="addr">地点和跟踪进度</div>
        </div>

        <div class="list">
          <div class="listItem" v-for="item,index in showList" :key="index" :class="{active:(sort&&index==0)||(((!sort)&&index==(showList.length-1)))}">
            <div class="liTime">
              <!-- 第一种分割方法，比较麻烦，slice -->
              <!-- <div class="data">{{item.time.slice(0,11)}}</div>
              <div class="hour">{{item.time.slice(11,16)}}</div> -->
              <!-- 第二种分割方法，常用，split -->
              <div class="data">{{item.time.split(" ")[0]}}</div>
              <div class="hour">{{item.time.split(" ")[1]}}</div>
            </div>
            <div class="liMid">
              <div class="circle" :class="{active:(sort&&index==0)||(((!sort)&&index==(showList.length-1)))}"></div>
              <div class="line"></div>
            </div>
            <div class="addrTxt">{{item.status}}</div>
          </div>
        </div>
      </div>
      <div v-else>
        <h3 style="color: #ccc;text-align: center">尚无搜索结果</h3>
      </div>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'

export default {
  name: 'Home',
  data:function(){
    return{
      value:"SF1980994583037",
      data:[],
      sort: true,
      isShowHistory:false,
      expressCode:[]
    }
  },
  mounted(){
    this.expressCode =localStorage.expressCode?JSON.parse(localStorage.expressCode):[]
  },
  computed:{
    showList(){
      if(this.sort){
        // console.log(this.sort)
        return [...this.data]
      }else{
        // console.log(this.sort)
        return [...this.data].reverse()
      }
    }
  },
  methods:{
    async search(){
      // 手机号后四位
      let phoneNum = '6029'
      let httpUrl = `https://qyexpress.market.alicloudapi.com/composite/queryexpress?mobile=${phoneNum}&number=${this.value}`
      let config = {
        headers:{
          "Authorization":"APPCODE 1153fde779fa4eb69a59c0301d961062"
        }
      }
      let res = await axios.get(httpUrl,config)
      let data = res.data.data.list
      this.data = data
      this.saveExpressHistory();
      // this.value = "";
      console.log(this.data)
    },
    // 封装成一个方法
    // 将搜索记录保存本地 
    saveExpressHistory(){
      // 首先从本地存储里查看是否有存放的历史快递单号，如果有就用Json解析出来，没有就创建一个新的数组
      this.expressCode = localStorage.expressCode?JSON.parse(localStorage.expressCode):[]
      // 判断数组是否已存在这个单号,indexOf返回寻找内容的下标，=-1说明没有找到
      if(this.expressCode.indexOf(this.value) == -1){
        this.expressCode.push(this.value)
        localStorage.expressCode = JSON.stringify(this.expressCode)
      }
    },
    chooseExpressCode(code){
      this.value = code;
      this.search();

      //  @blur="isShowHistory=false"使这个内容消失的太快，点不到 
      this.isShowHistory = false
    },
    cancelFocus(){
      setTimeout(()=>this.isShowHistory=false,50)
    }
  }
}
</script>

<style lang="scss">
