  @include background-image("../assets/topbanner.png");
  width: 100%;
  // 去掉，弹性布局，最后内容高过长，使得上面的内容压缩，input-div 变窄
  // height: 100vh;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  .input{
    width: 3.55rem;
    height: 0.48rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 0.05rem;
    box-shadow: 0 1px 10px #ccc;
    margin-top: 0.92rem;
    position: relative;
    .text{
      flex: 1;
      // height: 100%;
      border: none;
      outline: none;
      font-size: 0.16rem;
      padding: 0 0.2rem;
    }
    .close-icon{
      width: 0.20rem;
      height: 0.20rem;
      padding: 0 0.03rem;
      @include background-image('../assets/cancel.png');
      background-size: 80% auto;
      border-right: 1px solid #ccc;
    }
    .search-icon{
      width: 0.26rem;
      height: 0.26rem;
      margin: 0 0.08rem;
      @include background-image('../assets/search.png')
    }
  }
  $triColor : #ddd;
  // 向下的三角形
  @mixin triangleB($color:$triColor,$borderWidth:0.05rem){
    width: 0;
    height: 0;
    border-top: $borderWidth solid $color;
    border-bottom: $borderWidth solid transparent;
    border-left: $borderWidth solid transparent;
    border-right: $borderWidth solid transparent;
  }
  // 向上的三角形
  @mixin triangleT($color:$triColor,$borderWidth:0.05rem){
    width: 0;
    height: 0;
    border-top: $borderWidth solid transparent;
    border-bottom: $borderWidth solid $color;
    border-left: $borderWidth solid transparent;
    border-right: $borderWidth solid transparent;
  }
  // 最好控制在3-4层
  .searchResult{
    width: 100%;
    font-size: 0.14rem;
    .content{
      width: 100%;
      margin-top: 0.1rem;
      .header{
        width: 100vw;
        display: flex;
        justify-content: center;
        .time{
          width: 0.96rem;
          height: 0.2rem;
          display: flex;
          justify-content: center;
          .icon{
            width: 0.2rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .topT{
              @include triangleT;
              margin-bottom: 0.02rem;
              &.active{
                @include triangleT($font-color);
              }
            }
            .botT{
              @include triangleB;
